(function () {
    'use strict';

    angular
        .module('auth')
        .factory('authInterceptor', authInterceptor);

    /** @ngInject */
    function authInterceptor($log, $q, $window) {
        var Authentication = {
            request: onRequest,
            response: onResponse,
            responseError: onResponseError
        };

        return Authentication;

        ////////////////

        function onRequest(config) {
            var deferred = $q.defer();
            // promise that should abort the request when resolved.
            config.timeout = deferred.promise;
            config.cancel = deferred;
            return config;
        }

        function onResponse(response) {
            return response || $q.when(response);
        }

        function onResponseError(response) {
            if (response.status === 401) {
                // user is not authenticated
                $log.error('user is not authenticated', response);
                $window.location.href = '/#/login';
            }
            if (response.status === 403) {
                // user is not authenticated
                $log.warn('operation forbidden', response);
            }

            // if (response.status === 404) {
            //     $log.error('request is not found', response);
            //     $window.location.href = '/#/404';
            // }

            return $q.reject(response);
        }

    }
	
})();

(function () {
    'use strict';

    /**
     * @ngdoc service
     * @name auth.factory:principal
     * @description
     * # principal
     */
    angular
        .module('auth')
        .factory('principal', principal);

    /** @ngInject */
    function principal($q, $http, $log, $state, $rootScope, host) {
        var _identity;
        var identityDeffered;

        return {
            authorize: authorize,
            hasRole: hasRole,
            hasRoles: hasRoles,
            identity: identity,
            getIdentity: getIdentity,
            logout: logout
        };

        function authorize() {
            console.log('principal.authorize', $state.data);
            return identity().then(
                function () {
                    if ($state.data &&
                        $state.data.roles &&
                        $state.data.roles.length > 0
                        && !hasRoles($state.data.roles)) {
                        console.log('\tIn if')
                        if (_identity) {
                            console.log('user is signed in but not authorized for desired state');
                            // user is signed in but not authorized for desired state
                            $state.go('unauthorized');
                        }
                        else {
                            console.log('user is not authenticated!');
                            // user is not authenticated, go to unauthorized page
                            $rootScope.returnToState = $rootScope.toState;
                            $rootScope.returnToStateParams = $rootScope.toParams;
                            $state.go('unauthorized');
                        }
                    }
                },
                function () {
                    console.log('user not authorized, going to login');
                    $state.go('login');
                });
        }

        function hasRole(role) {
            if (!_identity || !_identity.roles) {
                $log.info('hasRole : !_identity.authorities');
                $log.info(_identity);
                return false;
            }
            for (var i = 0; i < _identity.roles.length; i += 1) {
                if (_identity.roles[i].role === role) {
                    return true;
                }
            }
            return false;
        }

        function hasRoles(roles) {
            // $log.info('hasRoles:' , roles);
            if (!_identity.authorities) {
                // $log.info('hasRoles: !_identity.authorities');
                // $log.info(_identity);
                return false;
            }
            for (var i = 0; i < roles.length; i += 1) {
                if (hasRole(roles[i])) {
                    return true;
                }
            }
            // $log.info('hasRoles: Role note found');
            return false;
        }

        function identity(force) {
            $log.info('identity function');

            if (force === true) {
                $log.info('_identity set to undefined');
                _identity = undefined;
                identityDeffered = undefined;
            }
            if (identityDeffered) {
                return identityDeffered.promise;
            }

            identityDeffered = $q.defer();

            // check and see if we have retrieved the identity data from the server.
            // if we have, reuse it by immediately resolving
            if (angular.isDefined(_identity)) {
                identityDeffered.resolve(_identity);
                $log.info('\t_identity is resolved');
                return identityDeffered.promise;
            }

            // otherwise, retrieve the identity data from the server,
            // update the identity object, and then resolve.
            // var url = host.url() + host.api + 'user/me';
            var url = host + 'api/user/me';
            // var url = '/data/me.json';
            $http.get(url, {'withC	redentials': true})
                .then(function (response) {
                    if (response.status === 200) {
                        $log.info('identity resolved');
                        _identity = response.data;
                        identityDeffered.resolve(_identity);
                        $rootScope.currentUser = _identity;
                    } else {
                        $log.info('identity resolved by strange status', response.status);
                        identityDeffered.reject(response.data);
                        identityDeffered = undefined;
                    }
                }, function (error) {
                    $log.info('identity rejected');
                    identityDeffered.reject(error);
                    identityDeffered = undefined;
                });

            return identityDeffered.promise;
        }

        function getIdentity() {
            return _identity;
        }

        function logout() {
            var url = host + 'api/logout';
            $http.get(url, {'withCredentials': true})
                .then(function (response) {
                    // console.log('logout successcallback', response);
                    if (response.status === 200) {
                        _identity = undefined;
                        identityDeffered = undefined;
                        console.log('logout successful', getIdentity());
                        $state.go('login');
                    } else {
                        $state.go('login');

                    }
                }, function () {
                    console.log('unsuccessful logout')
                });

        }
    }
})();

(function () {
    'use strict';

    angular
        .module('auth')
        .factory('RequestService', RequestService);

    /** @ngInject */
    function RequestService($http, $httpParamSerializer, $q, host) {
        return theService;

        function theService(base) {
            var url = host + base;
            // var url = base;

            var service = {
                'delete': delet,
                'get': get,
                'getById': getById,
                'getFile': getFile,
                'multipartPost': multipartPost,
                'multipartPut': multipartPut,
                'post': post,
                'put': put,
                'xPost': xPost,
                'xPut': xPut
            };

            return service;

            function delet(id, params) {
                var path = id ? (url + id ) : url;
                return $http.delete(path, {params: params})
                    .then(successCallback).catch(errorCallback);
            }

            function get(params) {
                var URL = url; //url[url.length - 1] === '/' ? url.substr(0, url.length - 1) : url;
                return $http.get(URL, {params: params})
                    .then(successCallback).catch(errorCallback);
            }

            function getFile(params) {
                var URL = url[url.length - 1] === '/' ? url.substr(0, url.length - 1) : url;
                return $http({
                    url: URL,
                    method: "GET",
                    params: params,
                    responseType: 'arraybuffer'
                }).then(successCallback).catch(errorCallback);
            }

            function getById(id, params) {
                return $http.get(url + id, {params: params})
                    .then(successCallback).catch(errorCallback);
            }

            function multipartPost(data, params) {
                var config = {
                    headers: {'Content-Type': undefined},
                    params: params,
                    transformRequest: transformRequest
                };
                var URL = url[url.length - 1] === '/' ? url.substr(0, url.length - 1) : url;
                return $http.post(URL, data, config)
                    .then(successCallback).catch(errorCallback);
            }

            function multipartPut(id, data, params) {
                var config = {
                    headers: {'Content-Type': undefined},
                    params: params,
                    transformRequest: transformRequest
                };
                var URL = id ? url + id : url;
                return $http.put(URL, data, config)
                    .then(successCallback).catch(errorCallback);
            }

            function post(data, params) {
                var URL = url;//url[url.length - 1] === '/' ? url.substr(0, url.length - 1) : url;
                return $http.post(URL, data, {params: params})
                    .then(successCallback).catch(errorCallback);
            }

            function put(id, data, params) {
                var URL = id ? url + id : url;
                return $http.put(URL, data, {params: params})
                    .then(successCallback).catch(errorCallback);
            }

            function xPost(data, params) {
                var config = {
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    params: params
                };
                var URL = url[url.length - 1] === '/' ? url.substr(0, url.length - 1) : url;
                return $http.post(URL, $httpParamSerializer(data), config)
                    .then(successCallback).catch(errorCallback);
            }

            function xPut(id, data, params) {
                var config = {
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    params: params
                };
                var URL = id ? url + id : url;
                return $http.put(URL, $httpParamSerializer(data), config)
                    .then(successCallback).catch(errorCallback);
            }

        }

        function errorCallback(response) {
            var message = (response && response.data && response.data.message) ?
                'APP.ERRORS.' + response.data.message : '';
            console.error('error', message);
            return $q.reject(response);
        }

        function successCallback(response) {
            if (response)
                return response.data;
            return {};
        }

        function transformRequest(data) {
            var formData = new FormData();
            for (var key in data) {
                if (data.hasOwnProperty(key)) {
                    formData.append(key, data[key]);
                }
            }
            return formData;
        }

    }
})();

(function () {
    'use strict';

    angular
        .module('auth')
        .config(routeConfig);

    /** @ngInject */
    function routeConfig($stateProvider) {
        $stateProvider
            .state('login', {
                controller: 'LoginController',
                controllerAs: 'vm',
                templateUrl: 'app/auth/login/login.html',
                url: '/login'
            })
            .state('register', {
                controller: 'RegisterController',
                controllerAs: 'vm',
                templateUrl: 'app/auth/register/register.html',
                url: '/register'
            })
            .state('forgetPass', {
                controller: 'ForgetPassController',
                controllerAs: 'vm',
                templateUrl: 'app/auth/forget-pass/forget-pass.html',
                url: '/forget-pass'
            })
            .state('resetPass', {
                controller: 'ResetPassController',
                controllerAs: 'vm',
                templateUrl: 'app/auth/reset-pass/reset-pass.html',
                url: '/reset-pass'
            })

            //errors states
            .state('unauthorized', {
                url: '/401',
                templateUrl: 'app/auth/errors/401.html'
            })
            .state('notFound', {
                url: '/404',
                templateUrl: 'app/auth/errors/404.html'
            })
            .state('internalServerError', {
                url: '/500',
                templateUrl: 'app/auth/errors/500.html'
            })

        ;
    }
	
})();

