(function () {
    'use strict';

    angular.module('BlurAdmin.theme')
        .run(themeRun);

    /** @ngInject */
    function themeRun($timeout, $rootScope, $state, layoutPaths, preloader, $q, baSidebarService, themeLayoutSettings, principal, host) {
        var whatToWait = [
            preloader.loadAmCharts(),
            $timeout(3000)
        ];

        var theme = themeLayoutSettings;
        if (theme.blur) {
            if (theme.mobile) {
                whatToWait.unshift(preloader.loadImg(layoutPaths.images.root + 'blur-bg-mobile.jpg'));
            } else {
                whatToWait.unshift(preloader.loadImg(layoutPaths.images.root + 'blur-bg.jpg'));
                whatToWait.unshift(preloader.loadImg(layoutPaths.images.root + 'blur-bg-blurred.jpg'));
            }
        }

        $rootScope.host = host;
        console.log('host', host);
        $q.all(whatToWait).then(function () {
            $rootScope.$pageFinishedLoading = true;
        });

        $timeout(function () {
            if (!$rootScope.$pageFinishedLoading) {
                $rootScope.$pageFinishedLoading = true;
            }
        }, 5000);

        $rootScope.$baSidebarService = baSidebarService;

        $rootScope.$on('$stateChangeStart',
            function (event, toState, toParams, fromState, fromParams, options) {
                if (toState.name === "login" || toState.name === 'root.adWizard')
                    return;
                principal.authorize(toState)
                /* .then(function () {
                 console.log('success authorize');
             }, function () {
                 console.error('error authorize');
             })*/

                // transitionTo() promise will be rejected with
                // a 'transition prevented' error
            });
        moment.loadPersian([{usePersianDigits: true}]);
    }

})();
