(function () {
    'use strict';

    angular
        .module('auth')
        .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController($log, $http, $state, host, baUtil, principal) {
        var vm = this;
        vm.title = 'LoginController';
        vm.host = host;
        vm.state = 0;
        vm.login = login;
        vm.sendNumber = sendNumber;
        var token = null;
        activate();

        ////////////////

        function activate() {
            $log.info('login module activated.');
        }

        function sendNumber() {
            vm.loading = true;
            // var url = '/data/getToken.json';
            var url = '/api/user/getCode?username=' + vm.number;
            $http({
                method: 'POST',
                url: url,
                withCredentials: true,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function (data) {
                token = data.token;
                vm.state = 1;
            }, function (error) {
                console.log(error);
                if (error.data)
                    console.log('error', error);
                vm.loading = false;
            });
        }

        function login() {
            vm.loading = true;
            var url = '/api/login?username=' + vm.number + '&password=' + vm.password;
            $http({
                method: 'POST',
                url: url,
                withCredentials: true,
                data: "token=" + token + '&number=' + vm.number,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function () {
                console.log('login successful');
                baUtil.toast('success', 'successful login message');
                principal.identity(true);
                $state.go('root.dashboard');
            }, function (error) {
                vm.loading = false;
                baUtil.toast('error', error.data.message);
            });
        }

        function registerAd() {

        }

    }
	
})();

