(function () {
    'use strict';

    angular.module('BlurAdmin.pages.order')
        .controller('AdWizardCtrl', AdWizardCtrl);

    /** @ngInject */
    function AdWizardCtrl($scope, $filter, $state, $http, $sce, baUtil, orderService, principal) {
        var vm = this;

        vm.personalInfo = {};
        vm.productInfo = {};
        var token = null;
        var imageCodes = [
            'REL',
            'WOM',
            'SPO',
            'NEW',
            'CAL',
            'MUS'
        ];
        vm.baWizardInterface = {};
        vm.state = 0; //    0: zero state, 1: number sent for login, 2: login successful, 3: info sent for registration, 4: registration successful
        vm.getLoginCode = getLoginCode;
        vm.login = login;
        vm.createOrder = createOrder;
        vm.getRegistrationCode = getRegistrationCode;
        vm.getCategoryImg = getCategoryImg;
        vm.calculatePrice = calculatePrice;
        vm.prepare = prepare;
        vm.clearImage = clearImage;
        vm.clearBannerLink = clearBannerLink;
        vm.calculateCPMPrice = calculateCPMPrice;
        vm.viewCountCaption = viewCountCaption;
        vm.trustedBannerLink = trustedBannerLink;
        vm.isLastSelectedCategory = isLastSelectedCategory;
        vm.viewCounts = [
            {value: 100000, caption: ' '},
            {value: 200000, caption: ' '},
            {value: 500000, caption: ' '},
            {value: 1000000, caption: ' '},
            {value: 2000000, caption: ' '},
            {value: 5000000, caption: ' '}
        ];
        vm.countFilter = $filter('viewCount');
        settleIdentity();
        console.log(vm.user);
        vm.arePersonalInfoPasswordsEqual = function () {
            return vm.personalInfo.confirmPassword && vm.personalInfo.password === vm.personalInfo.confirmPassword;
        };

        orderService.getChannelGroups().then(function (data) {
            vm.categories = data;
        });

        function clearImage() {
            console.log('clearImage');
            if (vm.order && vm.order.bannerLink && !vm.orderContextForm.url.$error.pattern) {
                vm.orderFile = null;
                vm.order.bannerCaption = null;
            }
        }

        function clearBannerLink() {
            console.log('clearBanner');
            vm.order.bannerLink = null;
        }

        function settleIdentity() {
            return principal.identity().then(function () {
                vm.user = principal.getIdentity();
                vm.identitySettled = true;
            }, function () {
                vm.identitySettled = true;
            });
        }
        function getLoginCode() {
            vm.loading = true;
            console.log('sending code ', vm.loginUsername);
            var url = '/api/user/getCode?username=' + vm.loginUsername;
            $http({
                method: 'POST',
                url: url,
                withCredentials: true,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function (data) {
                token = data.token;
                vm.state = 1;
                vm.loading = false;
                console.log('token received', token);
            }, function (error) {
                baUtil.toast('error', 'Error in sending your number for login');
                if (error.data)
                    console.log('error', error.data);
                vm.loading = false;
            });
        }

        function getRegistrationCode() {
            vm.loading = true;
            console.log('sending code ', vm.registerUsername);
            var url = '/api/user/register?username=' + vm.registerUsername + '&name=' + vm.registerName;
            $http({
                method: 'POST',
                url: url,
                withCredentials: true,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function (data) {
                token = data.token;
                vm.state = 3;
                vm.loading = false;
                console.log('token received', token);
            }, function (error) {
                vm.state = 0;
                vm.loading = false;
                if (error && error.data && error.data.message)
                    baUtil.toast('error', error.data.message);
                else
                    baUtil.toast('error', '     ');
                if (error.data)
                    console.log('error', error.data);
            });
        }

        function login(register) {
            vm.loading = true;
            var url = '/api/login?username=' + (register ? vm.registerUsername : vm.loginUsername) + '&password=' + (register ? vm.registrationCode : vm.loginCode);
            $http({
                method: 'POST',
                url: url,
                withCredentials: true,
                data: "token=" + token + '&number=' + vm.number,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(function () {
                console.log('login successful');
                // createOrder();
                settleIdentity().then(function () {
                    vm.baWizardInterface.removeTab(3);
                    vm.baWizardInterface.selectStep(3);
                });
                baUtil.toast('success', 'successful login message');
                vm.state = register ? 4 : 2;
            }, function (error) {
                vm.loading = false;
                baUtil.toast('error', error.data.message);
            });
        }

        function createOrder() {
            console.log('creating order');
            vm.loading = true;
            principal.identity().then(function () {
                vm.order.channelGroups = [];
                vm.categories.forEach(function (category) {
                    if (category.selected)
                        vm.order.channelGroups.push(category);
                });
                orderService.createOrder(vm.order).then(function (data) {
                    console.log('order created', data);
                    if (vm.orderFile)
                        orderService.setPhoto(data.id, vm.orderFile).then(afterCreateOrder, function (response) {
                            if (response.data && response.data.message)
                                baUtil.toast('error', response.data.message);
                            else
                                baUtil.toast('error', 'Error in setting order photo')
                        });
                    else
                        afterCreateOrder();
                }, function (response) {
                    if (response.data && response.data.message)
                        baUtil.toast('error', response.data.message);
                    else
                        baUtil.crudToast('error', 'create', 'order');
                })
            }, function () {
                baUtil.toast("error", "You should login before creating your order!")
            });

            function afterCreateOrder() {
                $state.go('root.my-order');
                baUtil.toast('success', 'Order submitted successfully');
                vm.loading = false;
            }

        }

        function getCategoryImg(category) {
            return imageCodes.indexOf(category.code) > -1 ? category.code : 'default';
        }

        /*
         * Calculates price based on Rial
         */
        function calculatePrice(viewCount, categories) {
            if (!viewCount)
                return;
            return (viewCount / 1000) * calculateCPMPrice(categories);
        }

        function prepare(text) {
            if (!text)
                return "";
            return text.split("\n").join("<br>");
        }

        function calculateCPMPrice(categories) {
            if (!categories || categories.length == 0)
                return 25000;
            var sum = 0;
            var count = 0;
            for (var i = 0; i < categories.length; i++) {
                if (categories[i].selected) {
                    sum += categories[i].basePrice;
                    count++;
                }
            }
            if (count == 0)
                return 25000;
            return sum / count;
        }

        function viewCountCaption(viewCount) {
            if (!viewCount)
                return;
            for (var i = 0; i < vm.viewCounts.length; i++)
                if (vm.viewCounts[i].value === viewCount)
                    return vm.viewCounts[i].caption;
            return '-';
        }

        function trustedBannerLink() {
            return vm.order && vm.order.bannerLink ? $sce.trustAsResourceUrl(vm.order.bannerLink + '?embed=1') : '';
        }

        function isLastSelectedCategory(index) {
            for (var i = index + 1; i < vm.categories.length; i++) {
                if (vm.categories[i].selected)
                    return false;
            }
            return true;
        }
    }

})();


