(function () {
    'use strict';

    angular.module('BlurAdmin.pages.order')
        .controller('OrderCtrl', OrderCtrl);


    /** @ngInject */
    function OrderCtrl($scope, $state, $stateParams, $uibModal, baUtil, orderService, host) {
        console.log(host);
        $scope.loading = true;
        $scope.goToDetail = goToDetail;
        $scope.action = action;
        $scope.showOrder = showOrder;
        $scope.getPayAddress = getPayAddress;
        $scope.isAdmin = $state.current.name === 'root.admin-order';
        $scope.isCustomer = !$scope.isAdmin;
        $scope.statuses = [
            {title: '', value: null},
            {title: baUtil.translate('CREATED'), value: 'CREATED'},
            {title: baUtil.translate('ACCEPTED'), value: 'ACCEPTED'},
            {title: baUtil.translate('REJECTED'), value: 'REJECTED'},
            {title: baUtil.translate('PAID'), value: 'PAID'},
            {title: baUtil.translate('STARTED'), value: 'STARTED'}
        ];
        $scope.serverFilter = function (tablestate) {
            console.log(tablestate);
            getOrders(tablestate);
        };

        console.log($stateParams);
        if ($stateParams.view === 'afterPay' && $stateParams.orderId) {
            orderService.getOrder($stateParams.orderId).then(function (order) {
                console.log(order);
                showOrder(order, true);
            }, function () {
                baUtil.crudToast('error', 'order', 'read');
            })

        }

        function getOrders(tableState) {
            var url = 'api/campaign';
            var params = {
                size: tableState.pagination.number,
                page: (tableState.pagination.start / tableState.pagination.number)
            };
            if (tableState.sort && tableState.sort.predicate) {
                var order = tableState.sort.reverse ? ',desc' : ',asc';
                params.sort = tableState.sort.predicate + order;
            }
            var searchObject = tableState.search.predicateObject || {};
            if (searchObject.status)
                searchObject.statuses = [searchObject.status];
            else
                searchObject.statuses = [];
            if ($scope.isAdmin)
                orderService.getOrders(null, searchObject, params).then(successCallback);
            else
                orderService.getMyOrders(params).then(successCallback);

            function successCallback(data) {
                $scope.loading = false;
                tableState.pagination.numberOfPages = data.totalPages;  //  TODO: set tatalPages
                tableState.pagination.start = data.number * data.size;  //  TODO: set start = number * size
                tableState.pagination.totalItemCount = data.totalElements;  //  TODO: set totalElements
                tableState.pagination.number = data.size;   // TODO: set number = size
                $scope.displayedData = [].concat(data.content); //  TODO: set content
                $scope.start = tableState.pagination.start;
            }
        }

        function goToDetail(id) {
            $state.go('root.order', {'id': id});
        }

        function action(order, action) {
            console.log('action', action);
            var modalInstance = $uibModal.open({
                animation: true,
                ariaLabelledBy: 'action',
                ariaDescribedBy: 'action',
                templateUrl: 'app/pages/order/action/action.html',
                controller: 'ActionCtrl',
                size: 'sm',
                resolve: {
                    order: function () {
                        return order;
                    },
                    action: function () {
                        return action;
                    }
                }
            });
            var nextStatus = {
                'lunch': 'STARTED',
                'approve': 'ACCEPTED',
                'reject': 'REJECTED',
                'pay': 'PAID',
                'cancel': 'CANCELED'
            };
            modalInstance.result.then(function (adId) {
                orderService[action](order.id, adId).then(function () {
                    for (var i = 0; i < $scope.displayedData.length; i++) {
                        if ($scope.displayedData[i].id === order.id) {
                            $scope.displayedData[i].status = nextStatus[action];
                        }
                    }
                }, function () {
                    baUtil.toast('error', 'error in ' + action + 'ing order');
                });
            });
        }

        function showOrder(order, afterPay) {
            console.log('showOrder', order.title);
            var modalInstance = $uibModal.open({
                animation: true,
                ariaLabelledBy: 'content',
                ariaDescribedBy: 'content',
                templateUrl: 'app/pages/order/content/content.html',
                controller: 'ContentCtrl',
                size: 'lg',
                resolve: {
                    order: function () {
                        return order;
                    },
                    afterPay: function () {
                        return afterPay;
                    },
                    payAddress: function () {
                        return getPayAddress(order)
                    }
                }
            });
        }

        function getPayAddress(order) {
            return '/api/order/' + order.id + '/pay';
        }

    }

})();

