(function () {
    'use strict';

    angular.module('BlurAdmin.theme')
        .service('baUtil', baUtil);

    /** @ngInject */
    function baUtil(toastr, toastrConfig, $filter) {

        this.isDescendant = function (parent, child) {
            var node = child.parentNode;
            while (node != null) {
                if (node == parent) {
                    return true;
                }
                node = node.parentNode;
            }
            return false;
        };

        this.hexToRGB = function (hex, alpha) {
            var r = parseInt(hex.slice(1, 3), 16);
            var g = parseInt(hex.slice(3, 5), 16);
            var b = parseInt(hex.slice(5, 7), 16);
            return 'rgba(' + r + ', ' + g + ', ' + b + ', ' + alpha + ')';
        };

        this.hasAttr = function (elem, attrName) {
            var attr = $(elem).attr(attrName);
            return (typeof attr !== typeof undefined && attr !== false);
        };

        this.translate = $filter('translate');

        this.toast = function (type, message, title, conf) {
            var defaultConfig = angular.copy(toastrConfig);
            if (!type)
                return;
            var types = ['success', 'error', 'info', 'warning'];
            type = type.toLowerCase();
            if (types.indexOf(type) < 0)
                return;
            if (!message)
                return;
            message = this.translate(message);
            title = title ? this.translate(title) : '';
            angular.extend(toastrConfig, conf);
            toastr[type](message, title);
            angular.extend(toastrConfig, defaultConfig);

        };

        this.crudToast = function (type, crud, entity, conf) {
            if (!type || !crud || !entity)
                return;
            var cruds = ['create', 'read', 'update', 'delete'];
            crud = crud.toLowerCase();
            if (cruds.indexOf(crud) < 0)
                return;
            var message = this.translate(crud + ' % ' + type + ' message');
            var entityT = this.translate(entity);
            message = message.replace('$1', entityT);
            this.toast(type, message, null, conf);
        }
    }
})();

