(function () {
    'use strict';

    angular.module('BlurAdmin.pages.admin')
        .controller('AdminAdCtrl', AdminAdCtrl);

    /** @ngInject */
    function AdminAdCtrl($scope, $state, RequestService, baUtil) {
        $scope.loading = true;
        $scope.smartTablePageSize = 10;
        $scope.ad = {};
        $scope.adTypes = [
            'BASIC',
            'LINK_ON_BANNER'
        ];
        $scope.ad.adType = $scope.adTypes[0];

        $scope.goToDetail = goToDetail;
        $scope.addAd = addAd;
        $scope.serverFilter = function (tablestate) {
            getAds(tablestate);
        };

        //
        getCampaigns();

        //


        function getCampaigns() {
            // var url = 'data/campaigns.json';
            var url = 'api/campaign';
            RequestService(url).get({page: 0, size: 1000}).then(function (data) {
                $scope.campaigns = data.content;
                $scope.ad.campaign = $scope.campaigns[0];
            });
        }

        function getAds(tableState) {
            $scope.loading = true;
            // var url = 'data/campaigns.json';
            var url = 'api/campaign/ad';
            var params = {
                size: tableState.pagination.number,
                page: (tableState.pagination.start / tableState.pagination.number)
            };
            if (tableState.sort && tableState.sort.predicate) {
                var order = tableState.sort.reverse ? ',desc' : ',asc';
                params.sort = tableState.sort.predicate + order;
            }
            RequestService(url).get(params).then(function (data) {
                $scope.loading = false;
                tableState.pagination.numberOfPages = data.totalPages;
                tableState.pagination.start = data.number * data.size;
                tableState.pagination.totalItemCount = data.totalElements;
                tableState.pagination.number = data.size;
                $scope.displayedData = [].concat(data.content);
            }, function () {
                $scope.loading = false;
                baUtil.crudToast('error', 'read', 'ad');
            });
        }

        function addAd(theForm) {
            $scope.loading = true;
            // var url = 'data/campaigns.json';
            var url = 'api/campaign/' + $scope.ad.campaign.id + '/ad';
            console.log($scope.ad);
            RequestService(url).post($scope.ad).then(function () {
                $scope.loading = false;
                $scope.ad = {};
                $scope.ad.adType = $scope.adTypes[0];
                $scope.ad.campaign = $scope.campaigns[0];
                theForm.$setPristine();
                getAds({
                    pagination: {
                        start: 0,
                        number: 10
                    },
                    sort: {
                        predicate: "lightBotAd.createDate",
                        reverse: true
                    }
                });
                baUtil.crudToast('success', 'create', 'ad');
            }, function (error) {
                $scope.loading = false;
                baUtil.crudToast('error', 'create', 'ad');
            });
        }

        function goToDetail(id) {
            $state.go('root.adList', {'id': id});
        }
    }

})();

