(function () {
    'use strict';

    angular.module('BlurAdmin.pages.admin')
        .controller('AdminCampaignCtrl', AdminCampaignCtrl);

    /** @ngInject */
    function AdminCampaignCtrl($scope, $state, $uibModal, RequestService, userService, baUtil) {
        $scope.loading = true;
        $scope.smartTablePageSize = 10;
        $scope.campaign = {};

        $scope.goToDetail = goToDetail;
        $scope.openEdit = openEdit;
        $scope.addCampaign = addCampaign;
        $scope.serverFilter = function (tablestate) {
            getCampaigns(tablestate);
        };

        //

        userService.getOrganizations({size: 1000, page: 0}).then(function (data) {
            $scope.organizations = data.content;
            $scope.campaign.organization = $scope.organizations[0];
        }, function () {
            baUtil.crudToast('error', 'read', 'organization');
        });

        //

        function getCampaigns(tableState) {
            // var url = 'data/campaigns.json';
            var url = 'api/campaign';
            var params = {
                size: tableState.pagination.number,
                page: (tableState.pagination.start / tableState.pagination.number)
            };
            if (tableState.sort && tableState.sort.predicate) {
                var order = tableState.sort.reverse ? ',desc' : ',asc';
                params.sort = tableState.sort.predicate + order;
            }
            RequestService(url).get(params).then(function (data) {
                $scope.loading = false;
                tableState.pagination.numberOfPages = data.totalPages;
                tableState.pagination.start = data.number * data.size;
                tableState.pagination.totalItemCount = data.totalElements;
                tableState.pagination.number = data.size;
                $scope.displayedData = [].concat(data.content);
                $scope.start = tableState.pagination.start;
            }, function () {
                baUtil.crudToast('error', 'read', 'campaign');
            });
        }

        function addCampaign(theForm) {
            // var url = 'data/campaigns.json';
            var url = 'api/campaign';
            // console.log($scope.campaign);
            RequestService(url).post($scope.campaign).then(function (data) {
                $scope.loading = false;
                $scope.campaign = {};
                getCampaigns({
                    pagination: {
                        start: 0,
                        number: 10
                    },
                    sort: {
                        predicate: "created",
                        reverse: true
                    }
                });
                theForm.$setPristine();
                baUtil.crudToast('success', 'create', 'campaign');
            }, function () {
                baUtil.crudToast('error', 'create', 'campaign');
            });
        }

        function goToDetail(id) {
            $state.go('root.adList', {'id': id});
        }

        function openEdit(campaign) {
            var modalInstance = $uibModal.open({
                animation: true,
                ariaLabelledBy: 'edit-campaign',
                ariaDescribedBy: 'edit-campaign',
                templateUrl: 'app/pages/admin/campaign/update/update-campaign.html',
                controller: 'UpdateCampaignCtrl',
                size: 'sm',
                resolve: {
                    campaign: function () {
                        return campaign;
                    },
                    organizations: function () {
                        return $scope.organizations;
                    }
                }
            });
            modalInstance.result.then(function (newCampaign) {
                for (var i = 0; i < $scope.displayedData.length; i++) {
                    if ($scope.displayedData[i].id === newCampaign.id) {
                        $scope.displayedData[i] = newCampaign;
                        break;
                    }
                }
                baUtil.crudToast('success', 'update', 'campaign');
            });
        }
    }

})();

