(function () {
    'use strict';

    angular.module('BlurAdmin.pages.campaign')
        .controller('AdStatsCtrl', AdStatsCtrl);

    /** @ngInject */
    function AdStatsCtrl($scope, RequestService, $stateParams, baConfig, layoutPaths, $filter, $uibModal) {
        var templateData = [
            {
                "date": "2013-01-16",
                "market1": 71,
                "market2": 75,
                "sales1": 5,
                "sales2": 8
            }
        ];
        $scope.loading = true;
        $scope.campaignName = $stateParams.campaignName;
        $scope.openDetails = openDetails;
        var layoutColors = baConfig.colors;
        // var url = 'data/ad.json';
        var url = 'api/ad/' + $stateParams.id;
        RequestService(url).get().then(function (data) {
            $scope.loading = false;
            // console.log('ad', data);
            $scope.ad = data;
        });

        // url = 'data/repView.json';
        url = 'api/ad/' + $stateParams.id + '/getReps';
        RequestService(url).get().then(function (data) {
            $scope.loading = false;
            console.log('repView', data);
            $scope.repView = data;
            $scope.overallViews = 0;
            data.forEach(function (ch) {
                $scope.overallViews += ch.views;
            })
        });

        // url = 'data/adStat.json';
        url = 'api/ad/' + $stateParams.id + '/getStats';
        RequestService(url).get().then(function (data) {
            $scope.loading = false;
            // console.log('adStat', data);
            var chartData = data.map(function (record, index, array) {
                var item = {};
                item.originalDate = record.time;
                item.showInAxis = new Date(item.originalDate).getHours() % 12 === 0;
                item.date = moment(record.time).format('YYYY-MM-DD H:m');
                item.visits = record.views;
                item.daily = index ? array[index].views - array[index - 1].views : 0;
                return item;
            });
            // console.log('adStat', chartData);
            $scope.adStat = data;


            var chart = AmCharts.makeChart("stat-chart", {
                "type": "serial",
                "theme": "light",
                "marginRight": 80,
                "dataProvider": chartData,
                "valueAxes": [{
                    "position": "left",
                    "title": " ",
                    labelFunction: function (value, valueText, valueAxis) {
                        return Math.floor(value / 1000) + 'K';
                    }
                }],
                "graphs": [{
                    "id": "g1",
                    "fillAlphas": 0.4,
                    "valueField": "visits",
                    "balloonText": "<div style='margin:5px; font-size:14px; font-family: Vazir, serif'> :<b>[[value]]</b></div>"
                },
                    {
                        "id": "g2",
                        "fillAlphas": 0.4,
                        "type": "column",
                        "valueField": "daily",
                        "balloonText": "<div style='margin:5px; font-size:14px;font-family: Vazir, serif'> :<b>[[value]]</b></div>"
                    }],
                "chartScrollbar": {
                    "graph": "g1",
                    "scrollbarHeight": 80,
                    "backgroundAlpha": 0,
                    "selectedBackgroundAlpha": 0.1,
                    "selectedBackgroundColor": "#888888",
                    "graphFillAlpha": 0,
                    "graphLineAlpha": 0.5,
                    "selectedGraphFillAlpha": 0,
                    "selectedGraphLineAlpha": 1,
                    "autoGridCount": true,
                    "color": "#AAAAAA"
                },
                "chartCursor": {
                    "categoryBalloonDateFormat": "JJ:NN, DD MMMM",
                    "cursorPosition": "mouse"
                },
                "categoryField": "date",
                "categoryAxis": {
                    // "minPeriod": "mm",
                    "parseDates": false,
                    forceShowField: 'showInAxis',
                    labelRotation: 45,
                    fontSize: 20,
                    minHorizontalGap: 2000,
                    title: '',
                    categoryFunction: function (category, dataItem, categoryAxis) {
                        // console.log(dataItem);
                        var format = new Date(dataItem.originalDate).getHours() === 0 ? 'jD-jMMMM' : 'HH\'';
                        if(dataItem.showInAxis)
                            console.log($filter('jDate')(dataItem.originalDate, format), dataItem.showInAxis, new Date(dataItem.originalDate).getHours());
                        return $filter('jDate')(dataItem.originalDate, format);
                        // return category;
                    }
                },
                "export": {
                    "enabled": true,
                    "dateFormat": "YYYY-MM-DD HH:NN:SS"
                }
            });

            chart.addListener("dataUpdated", zoomChart);
// when we apply theme, the dataUpdated event is fired even before we add listener, so
// we need to call zoomChart here
            zoomChart();

            // this method is called when chart is first inited as we listen for "dataUpdated" event
            function zoomChart() {
                // different zoom methods can be used - zoomToIndexes, zoomToDates, zoomToCategoryValues
                chart.zoomToIndexes(0, chartData.length);
            }

// generate some random data, quite different range
            function generateChartData() {
                var chartData = [];
                // current date
                var firstDate = new Date();
                // now set 500 minutes back
                firstDate.setMinutes(firstDate.getDate() - 1000);

                // and generate 500 data items
                var visits = 500;
                for (var i = 0; i < 500; i++) {
                    var newDate = new Date(firstDate);
                    // each time we add one minute
                    newDate.setMinutes(newDate.getMinutes() + i);
                    // some random number
                    visits += Math.round((Math.random() < 0.5 ? 1 : -1) * Math.random() * 10);
                    // add data item to the array
                    chartData.push({
                        date: newDate,
                        visits: visits
                    });
                }
                return chartData;
            };
        });

        function openDetails(channel) {
            console.log('openDetails', channel);
            var modalInstance = $uibModal.open({
                animation: true,
                ariaLabelledBy: 'details',
                ariaDescribedBy: 'details',
                templateUrl: 'app/pages/campaign/channelDetail/channelDetail.html',
                controller: 'ChannelDetailCtrl',
                size: 'md',
                resolve: {
                    id: function () {
                        return channel.hashId;
                    },
                    channel: function () {
                        return channel;
                    }
                }
            });

        }
    }

})();

