(function () {
    'use strict';

    angular.module('BlurAdmin.pages.dashboard')
        .controller('DashboardBarChartCtrl', DashboardBarChartCtrl);

    /** @ngInject */
    function DashboardBarChartCtrl(baConfig, layoutPaths, RequestService) {
        var layoutColors = baConfig.colors;
        var url = 'api/campaign/myCampaigns';

        var dataProvided = [];
        var dataclicks = false;
        RequestService(url).get({page: 0, size: 5}).then(function (data) {

            data.content.forEach(function (cmpgn) {
                console.log(cmpgn);
                var item = {
                    color: layoutColors.info,
                    clickColor: layoutColors.primary,
                    name: cmpgn.name,
                    views: cmpgn.views,
                    clicks: cmpgn.clicks
                };
                if (cmpgn.clicks)
                    dataclicks = true;
                dataProvided.push(item);
            });
            var valueAxes = [
                {
                    id: 1,
                    minimum: 0,
                    position: "left",
                    title: ' ',
                    gridAlpha: 0,
                    gridColor: layoutColors.border,
                    labelFunction: function (value, valueText, valueAxis) {
                        return Math.floor(value / 1000) + 'K';
                    }
                }
            ];
            var graphs = [
                {
                    balloonText: '<b>[[category]]: [[value]] </b>',
                    fillColorsField: 'color',
                    fillAlphas: 0.7,
                    lineAlpha: 0.2,
                    type: 'column',
                    valueField: 'views',
                    valueAxis: "1"
                }
            ];
            console.log(' ' + dataclicks);
            if (dataclicks) {
                valueAxes.push({
                    id: 2,
                    minimum: 0,
                    position: "right",
                    unitPosition: "left",
                    title: ' ',
                    gridAlpha: 0,
                    gridColor: layoutColors.border,
                    titleColor: layoutColors.primary,
                    labelColor: layoutColors.primary
                });
                graphs.push({
                    balloonText: '<b>[[category]]: [[value]]  </b>',
                    fillColorsField: 'clickColor',
                    fillAlphas: 0.7,
                    lineAlpha: 0.2,
                    type: 'column',
                    valueField: 'clicks',
                    valueAxis: "2"
                });
            }

            var barChart = AmCharts.makeChart('barChart', {
                type: 'serial',
                theme: 'blur',
                color: layoutColors.defaultText,
                dataProvider: dataProvided,
                valueAxes: valueAxes,
                startDuration: 1,
                graphs: graphs,
                chartCursor: {
                    categoryBalloonEnabled: false,
                    cursorAlpha: 0,
                    zoomable: false
                },
                categoryField: 'name',
                categoryAxis: {
                    gridPosition: 'start',
                    labelRotation: 45,
                    gridAlpha: 0.5,
                    gridColor: layoutColors.border,
                },
                export: {
                    enabled: true
                },
                creditsPosition: 'top-right',
                pathToImages: layoutPaths.images.amChart
            });
        });


    }
})();

