(function () {
    'use strict';

    angular.module('BlurAdmin.pages.user')
        .controller('OrganizationsCtrl', OrganizationsCtrl);

    /** @ngInject */
    function OrganizationsCtrl($scope, $state, userService, baUtil) {
        $scope.loading = true;
        $scope.smartTablePageSize = 10;
        $scope.organization = {};

        $scope.goToDetail = goToDetail;
        $scope.addOrganization = addOrganization;
        $scope.serverFilter = function (tablestate) {
            getOrganizations(tablestate);
        };

        //


        //

        function getOrganizations(tableState) {
            var params = {
                size: tableState.pagination.number,
                page: (tableState.pagination.start / tableState.pagination.number)
            };
            if (tableState.sort && tableState.sort.predicate) {
                var order = tableState.sort.reverse ? ',desc' : ',asc';
                params.sort = tableState.sort.predicate + order;
            }
            userService.getOrganizations(params).then(function (data) {
                $scope.loading = false;
                tableState.pagination.numberOfPages = data.totalPages;
                tableState.pagination.start = data.number * data.size;
                tableState.pagination.totalItemCount = data.totalElements;
                tableState.pagination.number = data.size;
                $scope.displayedData = [].concat(data.content);
            }, function () {
                baUtil.crudToast('error', 'read', 'organization');
            });
        }

        function addOrganization(theForm) {
            userService.addOrganization($scope.organization).then(function () {
                $scope.loading = false;
                $scope.organization = {};
                theForm.$setPristine();
                getOrganizations({
                    pagination: {
                        start: 0,
                        number: 10
                    },
                    sort: {
                        predicate: "id",
                        reverse: true
                    }
                });
                baUtil.crudToast('success', 'create', 'organization');
            }, function () {
                baUtil.crudToast('error', 'create', 'organization');
            });
        }

        function goToDetail(id) {
            $state.go('root.organization', {'id': id});
        }
    }

})();

