(function () {
    'use strict';

    angular.module('BlurAdmin.pages.user')
        .controller('UsersCtrl', UsersCtrl);

    /** @ngInject */
    function UsersCtrl($scope, $state, userService, baUtil, $uibModal) {
        $scope.loading = true;
        $scope.smartTablePageSize = 10;
        $scope.user = {
            isAdmin: false
        };

        $scope.goToDetail = goToDetail;
        $scope.addUser = addUser;
        $scope.openEdit = openEdit;
        $scope.serverFilter = function (tablestate) {
            getUsers(tablestate);
        };

        //

        userService.getOrganizations({size: 1000, page: 0}).then(function (data) {
            $scope.organizations = data.content;
            $scope.user.organization = $scope.organizations[0];
        }, function () {
            baUtil.crudToast('error', 'read', 'organization');
        });

        //

        function getUsers(tableState) {
            var params = {
                size: tableState.pagination.number,
                page: (tableState.pagination.start / tableState.pagination.number)
            };
            if (tableState.sort && tableState.sort.predicate) {
                var order = tableState.sort.reverse ? ',desc' : ',asc';
                params.sort = tableState.sort.predicate + order;
            }
            userService.getUsers(params).then(function (data) {
                $scope.loading = false;
                tableState.pagination.numberOfPages = data.totalPages;
                tableState.pagination.start = data.number * data.size;
                tableState.pagination.totalItemCount = data.totalElements;
                tableState.pagination.number = data.size;
                $scope.displayedData = [].concat(data.content);
            }, function () {
                baUtil.crudToast('error', 'read', 'user');
            });
        }

        function addUser(theFrom) {
            if (!$scope.user.organization)
                return;
            $scope.user.username = '09' + $scope.user.username;
            userService.addUser($scope.user).then(function () {
                $scope.loading = false;
                $scope.user = {
                    isAdmin: false
                };
                $scope.user.organization = $scope.organizations[0];
                theFrom.$setPristine();
                getUsers({
                    pagination: {
                        start: 0,
                        number: 10
                    },
                    sort: {
                        predicate: "name",
                        reverse: true
                    }
                });
                baUtil.crudToast('success', 'create', 'user');
            }, function () {
                $scope.user.username = $scope.user.username ? $scope.user.username.substring(2) : null;
                baUtil.crudToast('error', 'create', 'user');
            });
        }

        function goToDetail(id) {
            $state.go('root.user', {'id': id});
        }

        function openEdit(user) {
            var modalInstance = $uibModal.open({
                animation: true,
                ariaLabelledBy: 'edit-user',
                ariaDescribedBy: 'edit-user',
                templateUrl: 'app/pages/user/users/update/update-user.html',
                controller: 'UpdateUserCtrl',
                size: 'sm',
                resolve: {
                    user: function () {
                        return user;
                    },
                    organizations: function () {
                        return $scope.organizations;
                    }
                }
            });
            modalInstance.result.then(function (newUser) {
                for (var i = 0; i < $scope.displayedData.length; i++) {
                    if ($scope.displayedData[i].username === newUser.username) {
                        $scope.displayedData[i] = newUser;
                        break;
                    }
                }
                baUtil.crudToast('success', 'update', 'user');
            });
        }
    }

})();

