(function () {
    'use strict';

    angular.module('BlurAdmin.theme.components')
        .controller('baWizardCtrl', baWizardCtrl);

    /** @ngInject */
    function baWizardCtrl($scope) {
        var vm = this;
        vm.tabs = [];

        vm.tabNum = 0;
        vm.progress = 0;

        vm.addTab = function (tab) {
            tab.setPrev(vm.tabs[vm.tabs.length - 1]);
            vm.tabs.push(tab);
            vm.selectTab(0);
        };

        vm.removeTab = function (index) {
            console.log('removing', index);
            vm.tabs.splice(index, 1);
            vm.tabs.forEach(function (tab, index) {
                tab.setPrev(vm.tabs[index - 1]);
            });
            console.log(vm.tabs);
        };

        $scope.$watch(angular.bind(vm, function () {
            return vm.tabNum;
        }), calcProgress);

        vm.selectTab = function (tabNum) {
            console.log('selecting ', tabNum);
            vm.tabs[vm.tabNum].submit();
            if (vm.tabs[tabNum].isAvailiable()) {
                vm.tabNum = tabNum;
                vm.tabs.forEach(function (t, tIndex) {
                    if (tIndex == vm.tabNum) {
                        vm.nextTitle = t.nextTitle;
                        vm.hasNotNext = t.hasNotNext;
                        vm.hasNotPrevious = t.hasNotPrevious;
                        t.select(true);
                    } else {
                        t.select(false);
                    }

                });
            }
        };

        vm.isFirstTab = function () {
            return vm.tabNum == 0;
        };

        vm.isLastTab = function () {
            return vm.tabNum == vm.tabs.length - 1;
        };

        vm.nextTab = function () {
            if (vm.isLastTab())
                vm.tabs[vm.tabNum].action ? vm.tabs[vm.tabNum].action() : false;
            else
                vm.selectTab(vm.tabNum + 1)
        };

        vm.previousTab = function () {
            vm.selectTab(vm.tabNum - 1)
        };

        function calcProgress() {
            console.log('calc', vm.tabNum, vm.tabs, vm.progress, $scope.tabsn);
            if (!vm.tabs || vm.tabs.length == 0)
                vm.progress = ((vm.tabNum + 1) / $scope.tabsn) * 100;
            else
                vm.progress = ((vm.tabNum + 1) / vm.tabs.length) * 100;
        }

        $scope.internalcontrolInterface = $scope.controlInterface || {};
        $scope.internalcontrolInterface.selectStep = function (index) {
            vm.selectTab(index);
        }
        $scope.internalcontrolInterface.removeTab = vm.removeTab;
    }
})();


(function () {
    'use strict';

    angular.module('BlurAdmin.theme.components')
        .directive('baWizardStep', baWizardStep);

    /** @ngInject */
    function baWizardStep() {
        return {
            restrict: 'E',
            transclude: true,
            require: '^baWizard',
            scope: {
                form: '=',
                action: '=',
                nextTitle: '@',
                hasNotNext: '=',
                hasNotPrevious: '='
            },
            templateUrl: 'app/theme/components/baWizard/baWizardStep.html',
            link: function ($scope, $element, $attrs, wizard) {
                $scope.selected = true;
                // console.log(typeof $scope.action, $scope.action);
                var tab = {
                    title: $attrs.title,
                    iconClass: $attrs.iconClass,
                    select: select,
                    submit: submit,
                    isComplete: isComplete,
                    isAvailiable: isAvailiable,
                    action: $scope.action,
                    prevTab: undefined,
                    setPrev: setPrev,
                    nextTitle: $scope.nextTitle,
                    hasNotNext: !!$scope.hasNotNext,
                    hasNotPrevious: !!$scope.hasNotPrevious
                };

                wizard.addTab(tab);

                function select(isSelected) {
                    if (isSelected) {
                        $scope.selected = true;
                    } else {
                        $scope.selected = false;
                    }
                }

                function submit() {
                    console.log('Submitting ', tab.title);
                    $scope.form && $scope.form.$setSubmitted(true);
                }

                function isComplete() {
                    return $scope.form ? $scope.form.$valid : true;
                }

                function isAvailiable() {
                    return tab.prevTab ? tab.prevTab.isComplete() : true;
                }

                function setPrev(pTab) {
                    tab.prevTab = pTab;
                }
            }
        };
    }
})();
